.checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;
  
    .image-container {
      width: 23%;
      padding-right: 15px;
  
      img {
        width: 100%;
        height: 100%;
      }
    }
    .name {
        font-size: inherit;
        width: 20%;
    }

    .price {
        width: 23%;
        font-size: inherit;
    }
  
    .quantity {
      padding-left: 20px;
      width: 26%;
      display: flex;

      & > * {
          font-size: inherit;
      }

      .arrow {
          cursor: pointer;
      }

      .value {
          margin: 0 10px;
      }
    }
  
    .remove-button {
        font-size: inherit;
      padding-left: 12px;
      cursor: pointer;
      font-weight: bold;
    }
  }

  .quantity {
      font-size: 1.6rem;
  }
  